.App {
  background-color: #282c34;
  min-height: 100vh;
  color: white;
  padding: 0 2rem 2rem;
}

.content {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 2rem;
  min-height: 80vh;
  background-color: #222;
  width: 100%;
}

a {
  text-decoration: none;
}
.small-font {
  font-size: 0.9rem;
}

.red-font {
  color: red;
}